import React from "react";
import axios from "axios";
import Table from "./Table";

class Profile extends React.Component {
  constructor() {
    super();
    this.handleNameChange = this.handleNameChange.bind(this);
    this.state = {
      RepresentativesInfo: [],
      Name: "",
      filteredInfo: [],
    };
  }

  filterDesiredItem(name, arrToFilter) {
    console.log(name);
    console.log(arrToFilter);
    const representative = arrToFilter.filter((rep) => rep.FirstName === name);
    this.setState({ filteredInfo: representative });

    console.log(representative);
  }

  handleNameChange(Name) {
    this.setState({
      Name: Name,
    });

    this.filterDesiredItem(Name, this.state.RepresentativesInfo);
  }

  componentDidMount = () => {
    axios
      .get("/mock.json")
      .then((res) => {
        console.log(res.data);
        this.setState({ RepresentativesInfo: res.data });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  render() {
    const Name = this.state.name;

    const columns = [
      {
        Header: "General Info",

        columns: [
          {
            Header: "Id",
            accessor: "Id",
          },
          {
            Header: "FirstName",
            accessor: "FirstName",
          },
          {
            Header: "MiddleName",
            accessor: "MiddleName",
          },
          {
            Header: "LastName",
            accessor: "LastName",
          },
          {
            Header: "Gender",
            accessor: "Gender",
          },
          {
            Header: "DOB",
            accessor: "DOB",
          },
          {
            id: "MaritalStatus",
            Header: (props) => <span>Marital Status</span>,
            accessor: (d) => d.MaritalStatus.toString(),
          },
          {
            Header: "Spouse",
            accessor: "Spouse",
          },
          {
            Header: (props) => <span>Number Of Children</span>,
            accessor: "NumberOfChildren",
            Cell: (props) => <span className="number">{props.value}</span>,
          },
          {
            Header: "Education",
            accessor: "Education",
          },
        ],
      },
      {
        Header: "Details",

        columns: [
          {
            Header: "Affiliation",
            accessor: "Affiliation",
          },

          {
            Header: "AbsentDays",
            accessor: "AbsentDays",
            Cell: (props) => <span className="number">{props.value}</span>,
          },
        ],
      },
    ];

    return (
      <div className="Text">
        <Table columns={columns} data={this.state.RepresentativesInfo} />
      </div>
    );
  }
}

export default Profile;
